import {
	testAccount,
	testPassword
} from '@/utils/testInfo.js'

export function testLoginInfo(account, password, passwordAgain) {
	const results = [testAccount(account), testPassword(password, passwordAgain)]
	for (let i = 0; i < results.length; i++) {
		if (!results[i].valid) {
			uni.showToast({
				title: results[i].msg,
				icon: 'error'
			})
			return false
		}
	}
	return true
}
