import {
	testTel,
	testEmail,
	testPassword,
	testUserName,
	testCheckCode
} from '@/utils/testInfo.js'

export function testUserInfo({
	userName,
	tel,
	email,
	password,
	passwordAgain,
	checkCode
}) {

	const results = [testUserName(userName), testTel(tel), testEmail(email), testPassword(password,
		passwordAgain), testCheckCode(checkCode)]

	for (let i = 0; i < results.length; i++) {
		if (!results[i].valid) {
			uni.showToast({
				title: results[i].msg,
				icon: 'error'
			})
			return false
		}
	}

	return true
}
